export default{
	data(){
		return {
			modalName: null,
			imgList: [],
		}
	},
	methods:{
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		ChooseImage() {
			uni.chooseImage({
				count: 2, //默认9
				sizeType: ['original', 'compressed'], //可以指定是原图还是压缩图，默认二者都有
				sourceType: ['album'], //从相册选择
				success: (res) => {
					if (this.imgList.length != 0) {//如果提交了一张 imgList 会等于undefined
						this.imgList = this.imgList.concat(res.tempFilePaths)
					} else {
						this.imgList = res.tempFilePaths
					}
				}
			});
		},
		ViewImage(e) {
			uni.previewImage({
				urls: this.imgList,
				current: e.currentTarget.dataset.url
			});
		},
		DelImg(e) {//点击图片上面的×直接去掉那张图片
			this.imgList.splice(e.currentTarget.dataset.index, 1)
		},
		//实名提交
		Realnamesubmit(e){
			const _this = this
			let {name,idcard} = e.detail.value
			
			//正则匹配姓名和身份证
			let regname = /^[\u4E00-\u9FA5]{2,4}$/
			// 正则表达式：
			let idcardReg = /^[1-9]\d{7}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}$|^[1-9]\d{5}[1-9]\d{3}((0\d)|(1[0-2]))(([0|1|2]\d)|3[0-1])\d{3}([0-9]|X)$/;
			if(regname.test(name) && idcardReg.test(idcard) && this.imgList.length!=0){//当验证全部同过以后才能往下执行
				//进行上传图片

				uni.uploadFile({
					url:"http://hbk.huiboke.com/api/common/uploadImage?type=user",
					method:"POST",
					filePath:this.imgList[0],
					name:'file',
					fileType:"image",
					success:(resimg)=>{
						if(JSON.parse(resimg.data).code==0){
							let resdata = JSON.parse(resimg.data).data.src
							// resdata//这是返回了第一次图片
							uni.uploadFile({
								url:"http://hbk.huiboke.com/api/common/uploadImage?type=user",
								method:"POST",
								filePath:this.imgList[1],
								name:'file',
								fileType:"image",
								success(resimg2){
									if(JSON.parse(resimg2.data).code==0){
										let resdata2 = JSON.parse(resimg.data).data.src
										// resdata//这是返回了第一次图片
										//发起请求
										uni.request({
											url:"http://hbk.huiboke.com/api/user/submitRealNameAuthentication",
											method:"POST",
											data:{
												token:_this.tokey,
												real_name:name,
												id_card:idcard,
												fphoto:resdata,
												rphoto:resdata2
											},
											success:(resjieguo)=>{
												if(resjieguo.data.code){//提交成功
													_this.hideModal()
													uni.showToast({
														title:"认证成功",
														icon:"none"
													})
												}
											}
										})
									}
								}
							})
						}
					}
				})
				
			}
		}
	},
	props:["tokey"]
}
